import React from 'react'
import {Modal, Icon} from 'antd-mobile'
import { observer, inject } from 'mobx-react';
import EasyNarBar from '@/components/EasyNarBar/EasyNarBar';
require("./style.scss");
@inject('noticeDetailStore')
@observer
export default class Notice extends  React.Component<any>{
    
    componentDidMount(){
        const {noticeDetailStore:{inistore},location:{state:{MessData}}}=this.props;
        inistore(MessData);
    }
    componentWillUnmount(){
        const { noticeDetailStore:{clearData} }=this.props;
        clearData()
    }
    goback(){
        const{history}=this.props
       history.goBack();
    }
    render(){
        const { noticeDetailStore:{notice} }=this.props;
        
        return (
        <div className='noticeDiv'>
            <EasyNarBar
             mainContent="消息详情"
             leftContent={<Icon type="left" />}
            onLeftClick={this.goback.bind(this)}
            >
            </EasyNarBar>
            <div>
          <div>  <p className='Ptitle'>{notice.title}</p></div>
           <div className='noticeDate'><span>{notice.date}</span> <span>{notice.time}</span></div>
        <div className='contentDiv'>{notice.content}</div>
            </div>
        {notice.url?<div className='imgDiv'><img src={notice.url}></img></div>:''}
        </div>
        )}
} 